import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Project.vue';

Vue.use(Router);

const router = new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'project',
      component: Home,
      meta: {
        title: '项目管理',
      },
    },
    {
      path: '/white-list',
      name: 'whiteList',
      component: () => import('./views/White.vue'),
      meta: {
        title: '白名单管理',
      },
    },
    {
      path: '/device-manage',
      name: 'deviceMange',
      component: () => import('./views/DeviceManage.vue'),
      meta: {
        title: '设备管理',
      },
    },
    {
      path: '/hot-update',
      name: 'hotUpdate',
      component: () => import('./views/HotUpdate.vue'),
      meta: {
        title: '热更新资源管理',
      },
    },
  ],
});

export default router;
