<template>
  <div class="project">
    <div class="section-wrapper">
      <div class="table-controller">
        <div class="controller-list">
          <div class="controller-item">
            <Input
              v-model="filters.query"
              suffix="ios-search"
              placeholder="请输入搜索内容"
              @on-change="onInputParams"
              class="controller-input"
            />
          </div>
        </div>
        <!-- <div class="new-add">
              <Button type="primary" @click="add()">新增</Button>
            </div> -->
      </div>
      <Table style="margin: 15px 0" :loading="tableLoading" :columns="columns" :data="data">
        <template slot-scope="{ row }" slot="action">
          <Button type="success" @click="edit(row)">编辑</Button>
          <Button type="error" style="margin-left: 5px" @click="_delete(row)">删除</Button>
        </template>
      </Table>
      <div class="page">
        <Page
          :total="total"
          show-sizer
          show-total
          show-elevator
          @on-change="changePage"
          @on-page-size-change="changePageSize"
          :current="page"
          :page-size="pageSize"
        />
      </div>
    </div>
    <Modal @on-visible-change="closeModal" title="编辑项目" v-model="projectModal">
      <Form ref="form" :model="form" :rules="ruleValidate" :label-width="80">
        <FormItem label="项目ID" prop="code">
          <Input v-model="form.code" disabled  placeholder="输入项目ID" />
        </FormItem>
        <FormItem label="项目名称" prop="name">
          <Input v-model="form.name" placeholder="输入项目名称" />
        </FormItem>
      </Form>
      <template slot="footer">
        <Button @click="projectModal = false">取消</Button>
        <Button type="success" @click="save" :loading="loading">确定</Button>
      </template>
    </Modal>
  </div>
</template>

<script>
import { debounce } from '@/common/js/util';
import {
  checkCodeParam,
  getProjectList,
  editProject,
  deleteProject,
} from '@/api/project';

export default {
  data() {
    const validateCode = (rule, value, callback) => {
      const { field } = rule;
      if (this.title === '编辑项目' && this.form[field] === this.editParams[field]) {
        callback();
        return;
      }
      checkCodeParam(field, value).then((res) => {
        if (res > 0) {
          callback(new Error('已经存在相同的项目ID'));
          return;
        }
        callback();
      });
    };
    return {
      filters: {
        query: '',
      },
      tableLoading: false,
      data: [],
      columns: [
        {
          title: '项目ID',
          key: 'code',
          align: 'center',
        },
        {
          title: '项目名称',
          key: 'name',
          align: 'center',
        },
        {
          title: '操作',
          slot: 'action',
          align: 'center',
        },
      ],
      page: 1,
      pageSize: 10,
      total: 0,
      projectModal: false,
      loading: false,
      form: {
        code: '',
        name: '',
      },
      editParams: {},
      ruleValidate: {
        code: [
          { required: true, message: '请选择项目名称', trigger: 'blur' },
          { validator: validateCode, trigger: 'blur' },
        ],
        name: [{ required: true, message: '请选择项目名称', trigger: 'blur' }],
      },
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.tableLoading = true;
      getProjectList({ page: this.page, size: this.pageSize, query: this.filters.query })
        .then((res) => {
          const { list, total } = res;
          this.data = list;
          this.total = total;
          this.tableLoading = false;
        })
        .catch(() => {
          this.tableLoading = false;
        });
    },
    onInputParams: debounce(function () {
      this.getData();
    }, 600),
    changePage(val) {
      this.page = val;
      this.getData();
    },
    changePageSize(val) {
      this.pageSize = val;
      this.getData();
    },
    _delete(row) {
      this.$Modal.confirm({
        title: '删除项目',
        content: '<p>删除此项目，会删除此项目所有关联的白名单，确认是否删除此项目？</p>',
        onOk: () => {
          deleteProject(row.id)
            .then(() => {
              this.$Message.success('删除成功');
              this.getData();
            })
            .catch(() => {});
        },
      });
    },
    save() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true;
          editProject(this.form)
            .then(() => {
              this.$Message.success('编辑成功');
              this.getData();
              this.loading = false;
              this.projectModal = false;
            })
            .catch(() => {
              this.loading = false;
              this.projectModal = false;
            });
        }
      });
    },
    edit(row) {
      this.projectModal = true;
      this.form = { ...row };
      this.editParams = row;
    },
    closeModal(isShow) {
      if (isShow) {
        this.$refs.form.resetFields();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.project {
  .new-add {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 15px;
  }
  .page {
    display: flex;
    justify-content: flex-end;
  }
}
</style>
