import axios from './axios';

export function getHotSearchMap() {
  return axios.get('/hot-update/tree');
}
export function getList(params) {
  return axios.get('/hot-update/list', {
    params,
  });
}
export function getCurrentDetail(params) {
  return axios.get('/hot-update/app-version', {
    params,
  });
}
export function editRequest(params) {
  return axios.put('/hot-update', params);
}
export function deleteRequest(id) {
  return axios.delete('/hot-update', {
    params: { id },
  });
}
// 检查编辑状态从发布到过时或审核验证
export function checkStatus(id) {
  return axios.get('/hot-update/publish-check', {
    params: { id },
  });
}
