import * as types from './mutation-types';

const mutations = {
  [types.SET_ROUTE](state, route) {
    state.route = route;
  },
  [types.SET_ROUTE_PATH](state, path) {
    state.routePath = path;
  },
  [types.SET_USER](state, user) {
    state.user = user;
  },
  // 设置所有项目列表
  [types.SET_PROJECT](state, project) {
    state.projectList = project;
    const codeNameMap = {};
    project.forEach((item) => {
      codeNameMap[item.code] = item.name;
    });
    state.codeNameMap = codeNameMap;
  },

  // 设置所有设备
  [types.SET_DEVICE](state, device) {
    state.deviceList = device;
  },

  // 处理热更新筛选条件
  [types.SET_HOT_SEARCH](state, payload) {
    const gameList = Object.keys(payload);
    state.gameList = gameList;
    state.searchTree = payload;
  },
};

export default mutations;
