import axios from './axios';

export function getProjectAllList() {
  return axios.get('/project/all');
}
export function getProjectList(params) {
  return axios.get('/project/list', { params });
}
export function addProject(params) {
  return axios.post('/project', params);
}
export function editProject(params) {
  return axios.put('/project', params);
}
export function checkCodeParam(domain, value) {
  const url = '/project/check';
  return axios.get(url, {
    params: {
      domainField: domain,
      value,
    },
  });
}
export function deleteProject(id) {
  return axios.delete('/project', { params: { id } });
}
